import '@/global.css';
import { initPanel } from '@/panel';
import { initialize, manualOptimize, optimizeText, replaceMessage } from '@/core';

// 
// 
if (!(window as any).aiOptimizer) {
  (window as any).aiOptimizer = {};
}
Object.assign((window as any).aiOptimizer, {
  manualOptimize,
  optimizeText,
  replaceMessage,
});

$(() => {
  // 
  initialize();
  // UI
  initPanel();
});
