<template>
  <div class="ai-optimizer-settings">
    <div class="inline-drawer">
      <div class="inline-drawer-toggle inline-drawer-header">
        <b>{{ t`AI ` }}</b>
        <div class="inline-drawer-icon fa-solid fa-circle-chevron-down down"></div>
      </div>
      <div class="inline-drawer-content">
        <!--  -->
        <b>{{ t`` }}</b>
        <div class="block">
          <label>{{ t`Temperature` }} ({{ settings.temperature }})</label>
          <CustomSlider v-model="settings.temperature" :min="0" :max="2" :step="0.01" />
        </div>
        <div class="block">
          <label>{{ t`Max Tokens` }}</label>
          <input v-model.number="settings.max_tokens" class="text_pole" type="number" />
        </div>
        <div class="block">
          <label>{{ t`Top P` }} ({{ settings.top_p }})</label>
          <CustomSlider v-model="settings.top_p" :min="0" :max="1" :step="0.01" />
        </div>
        <div class="block">
          <label>{{ t`Top K` }}</label>
          <input v-model.number="settings.top_k" class="text_pole" type="number" />
        </div>

        <hr class="sysHR" />

        <!-- API  -->
        <b>{{ t`API ` }}</b>
        <div class="block">
          <label>{{ t`API ` }}</label>
          <select v-model="settings.apiProvider" class="text_pole">
            <option value="openai">OpenAI</option>
            <option value="openai_test">OpenAI ()</option>
            <option value="google">Google</option>
            <option value="sillytavern_backend">SillyTavern ()</option>
            <option value="sillytavern_preset">SillyTavern ()</option>
          </select>
        </div>
        <div class="block">
          <label>{{ t`API URL` }}</label>
          <input v-model="settings.apiUrl" class="text_pole" type="text" :placeholder="t`: https://api.openai.com/v1`" />
        </div>
        <div class="block">
          <label>{{ t`API ` }}</label>
          <input v-model="settings.apiKey" class="text_pole" type="password" />
        </div>
        <div class="button-group">
          <button class="menu_button" @click="handleTestConnection">{{ t`` }}</button>
          <button class="menu_button" @click="fetchModels">{{ t`` }}</button>
          <span v-if="connectionStatus === 'success'" style="color: green;">{{ t`` }}</span>
          <span v-if="connectionStatus === 'error'" style="color: red;">{{ t`` }}</span>
        </div>
        <div class="block">
          <label>{{ t`` }}</label>
          <select v-model="settings.modelName" class="text_pole">
            <option v-if="settings.modelName && !modelList.includes(settings.modelName)" :value="settings.modelName">
              {{ settings.modelName }} ()
            </option>
            <option v-for="model in modelList" :key="model" :value="model">
              {{ model }}
            </option>
          </select>
          <label class="checkbox_label">
            <input v-model="settings.autoFetchModels" type="checkbox" />
            {{ t`` }}
          </label>
        </div>

        <hr class="sysHR" />

        <!--  -->
        <b>{{ t`` }}</b>
        <div class="block">
          <label>{{ t` ( , )` }}</label>
          <textarea v-model="settings.disabledWords" class="text_pole" rows="3"></textarea>
        </div>
        <div class="block">
          <label>{{ t` ()` }}</label>
          <textarea v-model="settings.regexFilters" class="text_pole" rows="5"></textarea>
        </div>
        <div class="block">
          <label>{{ t` ()` }}</label>
          <textarea v-model="lastCharMessageContent" class="text_pole" rows="4" readonly></textarea>
        </div>
        <div class="button-group">
          <button class="menu_button" @click="handleFullAutoOptimize">{{ t`` }}</button>
          <label class="checkbox_label">
            <input v-model="settings.autoOptimize" type="checkbox" />
            {{ t`` }}
          </label>
          <label class="checkbox_label">
            <input v-model="settings.disableNotifications" type="checkbox" />
            {{ t`` }}
          </label>
        </div>
        <div class="block">
          <label>{{ t`` }}</label>
          <textarea v-model="optimizedContent" class="text_pole" rows="4"></textarea>
        </div>
        <div class="button-group">
          <button class="menu_button" @click="handleOptimize">{{ t`` }}</button>
        </div>
        <div class="block">
          <label>{{ t`` }}</label>
          <textarea v-model="optimizedResult" class="text_pole" rows="4"></textarea>
        </div>
        <div class="button-group">
          <button class="menu_button" @click="handleReplaceMessage">{{ t`` }}</button>
        </div>
        <div class="block">
          <label>{{ t` ()` }}</label>
          <textarea v-model="modifiedMessage" class="text_pole" rows="6"></textarea>
        </div>

        <hr class="sysHR" />

        <!--  -->
        <b>{{ t`` }}</b>
        <div class="block">
          <label>{{ t`:` }}</label>
          <select v-model="activePrompt" class="text_pole">
            <option value="main">{{ t` ()` }}</option>
            <option value="system">{{ t`` }}</option>
            <option value="final_system">{{ t`` }}</option>
          </select>
          <textarea v-model="settings.promptSettings[activePrompt]" class="text_pole" rows="6"></textarea>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
// Cache-busting comment
import CustomSlider from '@/components/CustomSlider.vue';
import { useSettingsStore } from '@/store/settings';
import { storeToRefs } from 'pinia';
import { onMounted, onUnmounted, ref, watch } from 'vue';
import { fetchModelsFromApi, testApiConnection, manualOptimize, optimizeText, replaceMessage, getLastCharMessage, checkMessageForDisabledWords } from '@/core';

const { settings } = storeToRefs(useSettingsStore());
const modelList = ref<string[]>([]);
const connectionStatus = ref<'unknown' | 'success' | 'error'>('unknown');
const activePrompt = ref<'main' | 'system' | 'final_system'>('main');
const optimizedContent = ref('');
const optimizedResult = ref('');
const modifiedMessage = ref('');
const lastCharMessageContent = ref('');
let messagePollingInterval: number | undefined;
let lastProcessedMessage = ref('');

// 
watch(lastCharMessageContent, (newMessage, oldMessage) => {
  if (!settings.value.autoOptimize || !newMessage || newMessage === lastProcessedMessage.value) {
    return;
  }

  // 
  if (checkMessageForDisabledWords(newMessage)) {
    console.log('[AI Optimizer] Detected disabled word in new message, triggering auto-optimization.');
    lastProcessedMessage.value = newMessage; // 
    handleFullAutoOptimize();
  }
});

const handleReplaceMessage = () => {
  if (!optimizedContent.value || !optimizedResult.value) {
    (toastr as any).warning('');
    return;
  }
  replaceMessage(
    optimizedContent.value,
    optimizedResult.value,
    (newContent: string) => {
      modifiedMessage.value = newContent;
    },
  );
};

const handleOptimize = async () => {
  if (!optimizedContent.value) {
    (toastr as any).warning('');
    return;
  }
  (toastr as any).info('AI...');
  try {
    const result = await optimizeText(optimizedContent.value, settings.value.promptSettings[activePrompt.value]);
    optimizedResult.value = result;
    (toastr as any).success('');
  } catch (error) {
    console.error(error);
    (toastr as any).error('');
  }
};

const fetchModels = async () => {
  (toastr as any).info('...');
  try {
    const models = await fetchModelsFromApi();
    if (models.length > 0) {
      modelList.value = models;
      (toastr as any).success(` ${models.length} `);
    } else {
      // The error is already shown by toastr in core.ts, so no need for another warning here.
      (toastr as any).warning('');
    }
  } catch (error) {
    console.error(error);
    (toastr as any).error('');
  }
};

const handleManualOptimize = () => {
  manualOptimize((content: string) => {
    optimizedContent.value = content;
    if (!content) {
      //  core.ts  manualOptimize 
      // (toastr as any).info('');
    }
  });
};

/**
 * 
 */
const handleFullAutoOptimize = async () => {
  try {
    (toastr as any).info('...');

    //  1: 
    const sourceContent: string = await new Promise((resolve) => {
      manualOptimize((content: string) => {
        resolve(content);
      });
    });

    // 
    if (!sourceContent) {
      console.log('[Auto Optimizer] ');
      (toastr as any).info('');
      return;
    }
    optimizedContent.value = sourceContent; // UI
    (toastr as any).success('AI...');

    //  2: AI
    const optimizedResultText = await optimizeText(sourceContent, settings.value.promptSettings[activePrompt.value]);
    if (!optimizedResultText) {
      throw new Error('AI ');
    }
    optimizedResult.value = optimizedResultText; // UI
    (toastr as any).success('AI...');

    //  3: 
    await new Promise<void>((resolve) => {
      replaceMessage(sourceContent, optimizedResultText, (newContent: string) => {
        modifiedMessage.value = newContent; // UI
        resolve();
      });
    });
    (toastr as any).success('', '', { timeOut: 5000 });

  } catch (error: any) {
    console.error('[Auto Optimizer] :', error);
    (toastr as any).error(error.message, '', { timeOut: 10000 });
  }
};

const handleTestConnection = async () => {
  connectionStatus.value = 'unknown';
  (toastr as any).info('...');
  try {
    const success = await testApiConnection();
    connectionStatus.value = success ? 'success' : 'error';
    if (success) {
      (toastr as any).success('API ');
    } else {
      (toastr as any).error('API ');
    }
  } catch (error) {
    connectionStatus.value = 'error';
    (toastr as any).error('API ');
    console.error(error);
  }
};

onMounted(() => {
  if (settings.value.autoFetchModels) {
    fetchModels();
  }

  // 
  lastCharMessageContent.value = getLastCharMessage();
  console.log('[AI Optimizer] Initial lastCharMessage:', lastCharMessageContent.value);

  messagePollingInterval = window.setInterval(() => {
    const newMessage = getLastCharMessage();
    console.log('[AI Optimizer] Polling for lastCharMessage:', newMessage);
    if (newMessage !== lastCharMessageContent.value) {
      lastCharMessageContent.value = newMessage;
      console.log('[AI Optimizer] lastCharMessage updated.');
    }
  }, 3000);
});

onUnmounted(() => {
  if (messagePollingInterval) {
    clearInterval(messagePollingInterval);
  }
});
</script>

<style scoped>
.block {
  display: flex;
  flex-direction: column;
  gap: 5px;
  margin: 10px 0;
}
.flex-container {
  display: flex;
  gap: 10px;
  align-items: center;
}
.flex-container > select {
  flex-grow: 1;
}
.button-group {
  display: flex;
  flex-direction: row;
  gap: 10px;
  align-items: center;
  margin: 10px 0;
}
.menu_button {
  white-space: nowrap;
}
</style>
